import React from "react"
import FadeIn from 'react-fade-in'
import styled from 'styled-components'

const Sidewalk = styled.img` 
    margin-top: 45.5vw;
    width: 100%;
    box-shadow: 0px 5px 8px #00000030;
    @media screen and (max-width: 600px) {
        display: none;
        visibility: hidden;
    }
`
const Arrow = styled.div` 
    width: 75vw;
    height: 2vw;
    margin: 0 auto;
    @media screen and (max-width: 600px) {
        position: absolute;
        width: 75vw;
       
        margin-top: 90vh;
        margin-left: 15vw;
        transform: scale(1.5)
        
 
    }
`
function Scene(){
    return (
        <div>
            <img className = "mahadvector" src = "/images/graphics/mahadvector2.svg" />
            <Sidewalk className = "sidewalk" src = "/images/graphics/sidewalk.svg" />
            <Arrow>
                <hr style = {{float: "left", width: "40%", opacity: "30%", margin: "1vw"}} />
                <img style={{float: "left", width: "2vw", margin: "1vw"}} src = "/images/icons/arrowicon.svg" />
                <hr style = {{float: "left", width: "40%", opacity: "30%", margin: "1vw"}} />
            </Arrow>
        </div>
    )
}

export default Scene