import React, { useEffect } from 'react'
import styled from 'styled-components'
import inViewport from "javascript-inviewport";

function Bar(props){
    var barwidth = props.width
    barwidth = barwidth.slice(0,barwidth.length - 2)
    console.log("barwidth :", barwidth)
    const GreyBar = styled.div` 
        width: 25vw;
        height: 0.75vw;
        background-color: #E9ECEF;
        margin-top: -0.5vw;
        margin-bottom: 0.25vw;
        border-radius: 4px;
        @media screen and (max-width: 600px) {
            width: 35vw;
            height: 1.5vw;
        }
    `
    const Skill = styled.div` 
        transition: width 3s ease-in-out;
        width: ${barwidth+'vw'};
        height: 0.75vw;
        background-color: #3FB66C;
        margin-top: -0.5vw;
        margin-bottom: 0.25vw;
        border-radius: 4px 0vw 0vw 4px;
        @media screen and (max-width: 600px) {
            width: ${barwidth*1.35 + 'vw'};
            height: 1.5vw;
        }

    `
    return(
        <GreyBar>
            <Skill />
        </GreyBar>
    )
}

export default Bar